import React, { useMemo } from 'react';
import { DateRange } from 'react-date-range';
import { useTranslation } from 'react-i18next';
import { zhTW, zhCN, enUS } from 'react-date-range/dist/locale';

const RecordDateRangeSearch = ({ ...restProps }) => {
  const { i18n } = useTranslation();
  const { language } = i18n;

  const getLocale = useMemo(() => {
    switch (language) {
      case 'zh-TW':
        return zhTW;
      case 'zh-CN':
        return zhCN;
      default:
        return enUS;
    }
  }, [language]);

  return (
    <DateRange
      editableDateInputs={true}
      dateDisplayFormat="yyyy-MM-dd"
      locale={getLocale}
      {...restProps}
    />
  );
};

export default RecordDateRangeSearch;
